@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
body {
  margin: 0;
  padding: 0;

  font-family: "Lato", sans-serif;
  background: #f5f6fa;
  color: #2f3640;
}

.ReactVirtualized__Grid__innerScrollContainer {
  padding-bottom: 20px;
}

header {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #2f3640;
    min-height: 200px;
    max-height: 216px;
  }
  
  h1, h5 {
    margin: 0;
    color: #f5f6fa;
    padding-top: 20px;
    text-align: center;
  }
  
  h1 {
    font-size: 4em;
  }
  
  h5 {
    margin: 0 auto;
    font-size: 14px;
    font-weight: lighter;
    max-width: 760px;
  }
  
  h1 a {
    text-decoration: none;
    color: inherit;
  }
  
  .header-input {
    max-width: 300px;
    width: 100%;
    margin: 15px auto 20px;
    height: 15px;
    border-radius: 4px;
    border-width: 0;
    padding: 8px;
    text-align: center;
    font-size: 1em;
  }
  
  @media (max-width: 450px) {
    h1 {
      font-size: 3em;
    }
    h5 {
      display: none;
    }
    header {
      min-height: 50px;
    }
  }
.card {
    height: 200px;
    background: #f5f6fa;
    
    margin: 40px 0;
  }
  
  .card-wrap {
    display: flex;
    flex-direction: column;
    
    max-width: 500px;
    border-radius: 10px;
    margin: 20px auto;
    background: white;
    box-shadow: -1px -1px 40px -18px rgba(0,0,0,0.78);
  }
  
  .card-image-wrap {
    height: 250px;
    width: 250px;
    
    margin: 0 auto;
    position: relative;
    top: -20px;
  }
  
  .card-image-wrap img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    box-shadow: 0px 5px 20px 0px rgba(50, 50, 50, 0.8);
  }
  
  .card-info {
    display: flex;
    flex-direction: column;
  }
  
  .card-info h2 {
    margin: 0 0 15px;
    text-align: center;
  }
  
  .card-info p {
    text-align: center;
    margin: 0 0 5px;
  }
  
  .card-info a {
    margin: 10px 0 15px;
    text-align: center;
    text-decoration: none;
    color: #0097e6;
  }
  
  .card-info a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 700px) { 
    .card-wrap {
      max-width: 370px;
    }
   }
  
   @media (max-width: 410px) { 
    .card-wrap {
      max-width: 320px;
    }
   }
  
   @media (max-width: 321px) { 
    .card-wrap {
         max-width: 300px;
    }
   }
.list-wrap {
  display: flex;
  flex-direction: column;
  
  height: calc(100vh - 216px);
}

@media (max-width: 450px) {
  .list-wrap {
    height: calc(100vh - 144px);
  }
}
.extended-card {
  height: 200px;
  background: #f5f6fa;
}

.extended-card-wrap {
  display: flex;
  flex-direction: column;
  
  max-width: 500px;
  border-radius: 10px;
  margin: 20px auto;
  background: white;
  box-shadow: -1px -1px 40px -18px rgba(0,0,0,0.78);
}

.extended-card-image-wrap {
  height: 250px;
  width: 250px;
  
  margin: 0 auto;
  position: relative;
  top: -20px;
}

.extended-card-image-wrap img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  box-shadow: 0px 5px 20px 0px rgba(50, 50, 50, 0.8);
}

.extended-card-info {
  display: flex;
  flex-direction: column;
}

.extended-card-info h2 {
  margin: 0 0 15px;
  text-align: center;
}

.extended-card-info p {
  text-align: center;
  margin: 0 0 10px;
}

.extended-card-info p:last-child {
  margin: 0 0 20px;
}

.extended-card-info a {
  margin: 10px 0 15px;
  text-align: center;
  text-decoration: none;
  color: #0097e6;
}

.extended-card-info a:hover {
  text-decoration: underline;
}


@media (min-width: 900px) { 
  .extended-card-wrap {
    max-width: 700px;
  }
 }

@media (max-width: 700px) { 
  .extended-card-wrap {
    max-width: 370px;
  }
 }

 @media (max-width: 410px) { 
  .extended-card-wrap {
    max-width: 320px;
  }
 }

 @media (max-width: 321px) { 
  .extended-card-wrap {
       max-width: 300px;
  }
 }
.gnome-detail {
  height: 100vh;
  background: #f5f6fa;
}

.gnome-detail-back {
  padding: 20px;
}

.gnome-detail-back a {
  padding: 20px 0;
  text-align: center;
  text-decoration: none;
  color: #0097e6;
}

.gnome-detail-back a:hover {
  text-decoration: underline;
}
.nomatch-wrap {
  padding-top: 20px;
}

.nomatch-h1 {
  color: #2f3640;
}

.nomatch-link {
  padding: 20px;
  text-align: center;
  text-decoration: none;
  color: #0097e6;
}

.nomatch-link a:hover {
  text-decoration: underline;
}
